                    },
                    theoretical: {
                        name: 'Estilo de aprendizaje terico.',
                        description: 'Los alumnos tericos adaptan e integran las observaciones que realizan en teoras\n' +
                        'complejas y bien fundamentadas lgicamente, piensan de forma secuencial y paso a paso, integrando hechos dispares en teora\n' +
