        /*Declaracin de metodos*/
        methods: {
            start_test() {
                this.started_test = true;
            },
            make_evaluation(min_index, max_index) {
                if (this.question_results.slice(min_index, max_index).includes(NaN)) {
                    this.snackbar = true
                } else {
                    this.active_total = 0;
                    this.reflexive_total = 0;
                    this.theoretical_total = 0;
                    this.pragmatic_total = 0;

                    this.active_index.forEach(index => {
                        this.active_total += parseInt(this.question_results[index - 1]);
                    });
                    this.reflexive_index.forEach(index => {
                        this.reflexive_total += parseInt(this.question_results[index - 1]);
                    });
                    this.theoretical_index.forEach(index => {
                        this.theoretical_total += parseInt(this.question_results[index - 1]);
                    });
                    this.pragmatic_index.forEach(index => {
                        this.pragmatic_total += parseInt(this.question_results[index - 1]);
                    });
                    this.evaluation_test = true;
                    this.series_spider = [{
                        name: 'Puntuacin',
                        data: [this.active_total, this.reflexive_total, this.theoretical_total, this.pragmatic_total]
                    }];
                    this.series_pie = [this.active_total, this.reflexive_total, this.theoretical_total, this.pragmatic_total];
                    this.max_total = Math.max(this.active_total, this.reflexive_total, this.theoretical_total, this.pragmatic_total);
